/* NAVBAR */

/* logo */

.logo {
  color: white;
  padding: 1px 6px;
  font-size: 1.35em;
  font-weight: bold;
  border-style: solid;
  border-color: White;
  border-width: 2px;
  margin: 20px auto 20px 15px;
}

.logo-2 {
  color: white;
  padding: 1px 6px 0;
  font-weight: bold;
  border-style: solid;
  border-color: White;
  border-width: 2px;
  margin: 20px auto 20px 15px;
}

.mt-logo {
  margin: 3px 0;
}

.individual-link {
  margin: 0 2vw;
}

.header {
  background-color: #0091a0;
  /* background-color: #192e6c; */
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  z-index: 1;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.header.transition {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.header a {
  text-decoration: none;
  font-family: 'roboto';
  color: white;
  text-shadow: 1px 1px 1px #002145;
  font-size: 1.1em;
}

@media (min-width: 400px) and(max-width: 500px) {
  .individual-link {
    margin: 0 1vw !important;
  }
}

@media (max-width: 393px) {
  .individual-link {
    margin: 0 3.5vw !important;
  }
}

@media (max-width: 362px) {
  .individual-link {
    margin: 0 1.9vw !important;
  }
}

