}

.color-bar {
  /* TUESDAY */
  width: 63.5%;

  /* WEDNESDAY */
  /* width: 75.6%; */

  /* THURSDAY */
  /* width: 67%; */

  /* FRIDAY */
  /* width: 54%; */

  /* SATURDAY */
  /* width: 65%; */

  /* SUNDAY */
  /* width: 59%; */

  margin: 14% auto;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(red),
    color-stop(orange),
    color-stop(#fde541),
    color-stop(green),
    color-stop(blue),
    color-stop(indigo),
    to(violet)
  );
  background: linear-gradient(
    to right,
    red,
    orange,
    #fde541,
    green,
    blue,
    indigo,
    violet
  );
  -webkit-background-clip: text;
}

.black-bar {
  pointer-events: none;
  background: black;
  -webkit-background-clip: text;
  -webkit-animation: loading 15s infinite;
          animation: loading 15s infinite;
}

#day {
  pointer-events: none;
  white-space: nowrap;
  position: static;
  color: transparent;
}

@-webkit-keyframes loading {
  from {
    left: 0;
    width: 100%;
  }
  2% {
    left: 0;
    width: 100%;
  }
  6% {
    left: 0;
    width: 0;
  }
  22% {
    left: 0;
    width: 0;
  }
  24% {
    left: 0;
    width: 100%;
  }
  100% {
    left: 0;
    width: 100%;
  }
}

@keyframes loading {
  from {
    left: 0;
    width: 100%;
  }
  2% {
    left: 0;
    width: 100%;
  }
  6% {
    left: 0;
