.home-typewriter-setup {
  /* position: relative;
    width: 29em;
    margin: 0 auto;
    text-align: center;
    white-space: nowrap;
    overflow: hidden; */
  position: relative;
  width: 24em;
  margin: 10vh auto;
  border-right: 3px solid rgba(255, 255, 255, 0.75);
  font-size: 180%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}

.home-typewriter-animation {
  -webkit-animation: typewriter 4s steps(65) 1s 1 normal both,
    blinkTextCursor 1000ms steps(40) infinite normal;
          animation: typewriter 4s steps(65) 1s 1 normal both,
    blinkTextCursor 1000ms steps(40) infinite normal;
}

@-webkit-keyframes typewriter {
  from {
    width: 0;
  }
  to {
    width: 29.5em;
  }
}

@keyframes typewriter {
  from {
    width: 0;
  }
  to {
    width: 29.5em;
  }
}
@-webkit-keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}
@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}

